import React, { useState, useEffect, useRef } from "react";
import io from "socket.io-client";
import { apiBaseUrl, MY_TURN_SERVER } from "config";
import styled from "styled-components";
import { getCookie } from "../utils/cookieUtils";

const RemoteVideo = styled.video`
  width: 100%;
  max-width: 400px; /* Adjust the maximum width as needed */
  border: 2px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.3s ease; /* Smooth transition for border color */

  /* Cool effects on hover */
  &:hover {
    border-color: #ff6b6b; /* Change border color on hover */
    box-shadow: 0 0 10px rgba(255, 107, 107, 0.5); /* Add shadow effect on hover */
  }
`;
const RemoteVideoContainer = styled.div`
  display: flex;
  flex-direction: row;
`;
const LocalVideo = styled.video`
  width: 100%;
  max-width: 200px; /* Adjust the maximum width as needed */
  border: 2px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.3s ease; /* Smooth transition for border color */

  /* Cool effects on hover */
  &:hover {
    border-color: #ff6b6b; /* Change border color on hover */
    box-shadow: 0 0 10px rgba(255, 107, 107, 0.5); /* Add shadow effect on hover */
  }
`;
const WebCam = ({ users, currentUserData, socket }) => {
  const [localStream, setLocalStream] = useState(null);
  const [newOffer, setNewOffer] = useState(null);
  const [videoStream, setVideoStream] = useState(null);
  const [remoteStreams, setRemoteStreams] = useState([]);
  const localVideoRef = useRef(null);
  const [remoteVideoRefs, setRemoteVideoRefs] = useState([]);
  const [userStreamList, setUserStreamList] = useState(new Map());
  const [peerList, setPeerList] = useState([]);
  const socketIdRef = useRef(null);

  const peerConnection = useRef(null);
    const initPeerConnection = () => {
    const configuration = {
      iceServers: [
        {
          urls: `${MY_TURN_SERVER}`,
          username: "ekla",
          credential: "12345678",
        },
      ],
    };
    const pc = new RTCPeerConnection(configuration);
    peerConnection.current = pc;


    }
  const renewOfferLocalStream = () => {
    // Send offer to server
if (localStream) {
    

    peerConnection.current
      .createOffer()
      .then((offer) => {
        setNewOffer(offer);
        console.log("setting new offer:", newOffer);
        peerConnection.current.setLocalDescription(newOffer);
      })

      .then(() => {
        console.log("new offer", peerConnection.current.localDescription);
        socket.emit("offer", peerConnection.current.localDescription);
      })
      .catch((error) => {
        console.error("Error creating offer:", error);
      });
}};
  const getNewStreams = () => {
    peerConnection.current.ontrack = (event) => {
      const stream = event.streams[0];
      // console.log("Streams",event.streams)
      // console.log("Tracks",event.streams[0].getTracks())
      setVideoStream(stream);
      if (event.streams[0]) {
        console.log("Stream List on user joined", userStreamList);
        if (!userStreamList.has(socketIdRef.current)) {
          setUserStreamList((prevMap) =>
            prevMap.set(socketIdRef.current, stream)
          );
        }

        console.log("stream:", stream);
      }
    };
  };
  useEffect(() => {
      if (peerConnection.current && users[users.length? users.length - 1: null] !== currentUserData.username ) {
    console.log("new user joined, sending new offer stream");
      renewOfferLocalStream();
      console.log("new users", users);
          
    }
        }, [users,currentUserData,peerConnection]);
  useEffect(() => {
    socket.on("userLeft", (data) => {
      if (userStreamList.has(data.socketId)) {
        console.log(
          "droping stream of value:",
          userStreamList.get(data.socketId),
          data.socketId
        );

        setRemoteVideoRefs((prevList) =>
          prevList.filter(
            (element) =>
              element.current.srcObject !== userStreamList.get(data.socketId)
          )
        );
        setRemoteStreams((prevList) =>
          prevList.filter(
            (element) => element !== userStreamList.get(data.socketId)
          )
        );
      }

      console.log(
        "droping stream",
        userStreamList.get(data.socketId),
        videoStream,
        remoteVideoRefs
      );
    });
  }, [socket, remoteVideoRefs, userStreamList, videoStream]);
  useEffect(() => {
    if (videoStream) {
      console.log("adding element to RemoteStreams array");
      setRemoteStreams((prevStreams) => [...prevStreams, videoStream]);
    }
  }, [videoStream, socket]);

  useEffect(() => {
initPeerConnection();
    peerConnection.current.onicecandidate = (event) => {
      if (event.candidate) {
        // Send ICE candidate to the server
        socket.emit("iceCandidate", event.candidate);
      }
    };
    navigator.mediaDevices
      .getUserMedia({ video: true, audio: true })
      .then((stream) => {
        setLocalStream(stream);
        localVideoRef.current.srcObject = stream;

        // Create peer connection
        if (stream) {
          const tracks = stream.getTracks();
          console.log("Tracks from the client stream",tracks);

          // Add each track to the peer connection
          tracks.forEach((track) => {
            peerConnection.current.addTrack(track, stream);
          });
          console.log("PC:", peerConnection.current);
if(peerConnection.current.localDescription){
    console.log("an offer exist already")
    socket.emit("offer",peerConnection.current.localDescription)
}
else{
          // Send offer to server
          peerConnection.current
            .createOffer()
            .then((offer) => {


              socket.emit("offer", offer);
                peerConnection.current.setLocalDescription(offer)})
            .then(() => {
            })
            .catch((error) => {
              console.error("Error creating offer:", error);
            });
}}
      })
      .catch((error) => {
        console.error("Error accessing media devices:", error);
      });

    return () => {
      if (localStream) {
        localStream.getTracks().forEach((track) => {
          track.stop();
        });
      }
    };
  }, [socket]);

  // Handle signaling messages
  useEffect(() => {
    socket.on("offer", (offer) => {
      console.log("Received offer:", offer);
      console.log("newOffer change to:", offer);
      socketIdRef.current = offer.id;
      if (!peerConnection.current) {
        console.log("no peerConnection.current");
        return;
      } else {
        const sessionDescription = new RTCSessionDescription(offer.offer);
        peerConnection.current.setRemoteDescription(sessionDescription);
        console.log("SDP", sessionDescription);

        // Create answer
        peerConnection.current
          .createAnswer()
          .then((answer) => peerConnection.current.setLocalDescription(answer))
          .then(() => {
            socket.emit("answer", peerConnection.current.localDescription);
          })
          .catch((error) => {
            console.error("Error creating answer:", error);
          });
      }
    });

    socket.on("answer", (answer) => {
      console.log("Received answer:", answer);

      if (!peerConnection.current) {
        console.log(
          "on answer failed to setRemoteDescription, no peerConnection.current",
          peerConnection.current
        );
        return;
      }
      peerConnection.current.setRemoteDescription(
        new RTCSessionDescription(answer)
      );
    });

    socket.on("iceCandidate", (iceCandidate) => {
      console.log("ice candidate:", iceCandidate);
      if (!peerConnection.current) return;
      peerConnection.current.addIceCandidate(new RTCIceCandidate(iceCandidate));
    });

    return () => {
      socket.off("offer");
      socket.off("answer");
      socket.off("iceCandidate");
      peerConnection.current.close();
    };
  }, [socket]);

  // Add remote stream to state when track is received
  useEffect(() => {
    if (!peerConnection.current) return;

    getNewStreams();
    return () => {
      peerConnection.current.ontrack = null;
    };
  }, []);
  useEffect(() => {
    if (peerConnection.current) {
      console.log("PC attributes:", peerConnection.current);
      getNewStreams();
    }
    return () => {
      peerConnection.current.ontrack = null;
    };
  }, [peerConnection]);

  useEffect(() => {
    if (remoteStreams.length > 0) {
      remoteStreams.map(() =>
        setRemoteVideoRefs((prevRefs) => [...prevRefs, React.createRef()])
      );
    }
    // Initialize refs array
  }, [remoteStreams, remoteStreams.length]);
  useEffect(() => {
    // Update srcObject for each remote video
    remoteStreams.forEach((stream, index) => {
      if (stream) {
        const videoRef = remoteVideoRefs[index];
        if (videoRef && videoRef.current) {
          console.log("single stream added to VidRemote Reference", stream);
          videoRef.current.srcObject = stream;
        }
      }
    });
  }, [remoteStreams, remoteVideoRefs]);

  return (
    <div>
      <LocalVideo
        id="localVideo"
        ref={localVideoRef}
        autoPlay
        muted
      ></LocalVideo>
      <RemoteVideoContainer>
        {remoteVideoRefs.map((refStream, index) =>
          refStream !== null ? (
            <RemoteVideo key={index} autoPlay ref={refStream}></RemoteVideo>
          ) : (
            <></>
          )
        )}
      </RemoteVideoContainer>
    </div>
  );
};

export default WebCam;
